import React, { Component } from 'react'
import styled from 'styled-components'

const Footer = styled.footer`
 background: #F9F8F9;
 border-top: 1px solid #ebebeb;
 padding: 1em;`

const Content = styled.div`
  text-align: center;`

export default () => (
  <Footer>
    <Content>
      <div>
        <a href="https://github.com/monsterooo/react-amap-sdk">react-amap-sdk</a> 文档
      </div>
    </Content>
  </Footer>
)


