import React, { Component } from 'react'
import styled from 'styled-components'
import Header from '../components/Header'
import Footer from '../components/Footer'
import Editor from '../components/Editor'
import Button from '../components/Button'
import ProgramWindow from '../components/ProgramWindow'

const Hero = styled.div`
  z-index: 1;
  background-image: linear-gradient(
    120deg,
    ${props => props.theme.colors.primary} 0%,
    #68b8fe 100%
  );
  color: #fff;
  padding: 70px;
  border-radius: 0 0 50% 50% / 4%;
  display: flex;
  justify-content: space-between;

  section:first-child {
    width: 50%;
    margin-left: 3%;
  }

  section:last-child {
    width: 45%;
  }

  h2 {
    margin-top: 1.5rem;
    font-size: 3.5em;
    margin-bottom: 0.25em;
    font-weight: bold;
    letter-spacing: 1.5px;
    line-height: 1.05em;
    display: inline-block;
  }

  h3 {
    font-size: 1.5em;
    margin-top: 0;
    margin-bottom: 1.5em;
    font-weight: 300;
  }

  @media ${props => props.theme.tablet} {
    padding: 50px 15px;
    flex-direction: column;

    h2 {
      font-size: 2.5em;
    }

    h3 {
      font-size: 1.25em;
    }

    section:first-child, section:last-child {
      width: 100%;
    }

    section:first-child {
      margin: 0 0 2em 0;
    }
  }
`

const PreviewButton = styled(Button)`
  position: absolute;
  bottom: 1em;
  right: 2em;
  z-index: 44;
  margin-top: 1em;
  box-shadow: 0 2px 5px 0px rgba(43, 63, 76, 0.18);
  border: 1px solid #eeeeee;

  &:hover {
    box-shadow: 0 3px 7px 0px rgba(43, 63, 76, 0.2);
  }
`
const Philosophy = styled.section`
  background: #f9f8f9;
  display: flex;
  justify-content: space-around;
  margin-top: -2em;
  padding: 5em 2em 2em 2em;

  div {
    width: 30%;
  }

  h3 {
    color: ${props => props.theme.colors.primary};
  }

  p {
    margin-top: 0;
  }

  @media ${props => props.theme.tablet} {
    flex-direction: column;

    div {
      width: 90%;
      margin: auto;
    }
  }
`

class IndexPage extends Component {
  componentDidMount() {
    localStorage.setItem('homeEditor', previewHEML)
  }

  render() {
    return (
      <div>
        <Header currentPath={this.props.location.pathname} fixed />
        <Hero>
          <section>
            <h2>React组件化高德地图</h2>
            <h3>
              react-amap-sdk是一个React组件化高德地图SDK，核心目标是在React中提供简单、与高德地图api一致的React组件。
            </h3>
            <Button to="/docs/getting-started/guide" large>
              使用指南
            </Button>
            <Button to="/docs/components/map" large transparent>
              项目文档
            </Button>
          </section>
          <section>
            <ProgramWindow title="基本示例">
              <Editor
                value={previewHEML}
                setOptions={{ minLines: 20, maxLines: 20 }}
                onChange={html => localStorage.setItem('homeEditor', html)}
              />
              {/* <PreviewButton to="https://codesandbox.io/s/k904qv023v">在线预览</PreviewButton> */}
            </ProgramWindow>
          </section>
        </Hero>
        <Philosophy>
          <div>
            <h3>易用性</h3>
            <p>
              所有组件的属性都遵循高德地图SDK的文档(事件除外), 让您在使用组件时可以100%的使用高德地图SDK中提供的所有选择。不必记忆额外的东西
            </p>
          </div>
          <div>
            <h3>扩展性</h3>
            <p>
              高德地图SDK核心的就是map组件，我们为所有的子组件都注入的map对象。让您能非常方便的扩展自己的地图组件
            </p>
          </div>
        </Philosophy>
        <Footer />
      </div>
    )
  }
}

const previewHEML = `import React, { Component } from 'react'

import { Map, Marker } from 'react-amap-sdk'

class Example extends Component {
  render () {
    return (
      <Map
        mapKey='您的高德地图key'
        version='1.4.8'
        width={300}
        height={300}
        center={center}
      >
        <Marker
          icon={icon}
          position={position}
          onClick={e => {
            console.log('我被点击了', e.target)
          }}
        />
      </Map>
    )
  }
}
`

export default IndexPage



